// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";


.main-screen__block-logo{
    color: #303030;
    font-size: 140px;
    text-transform: uppercase;
    line-height: 1.55;
    font-weight: 800;
    span{
        color: #f8d225;
    }
   
}
.main-screen__block-info{
    display: flex;
}
.main-screen__left-text{
    color: #000000;
    font-size: 30px;
    line-height: 1.55;
    font-weight: 400;
   span{
    color: #9789aa;
   }
}
.main-screen__left-arrow{
    display: flex;
    align-items: flex-start;
    img{
        max-width: 59px;
        cursor: pointer;
        transition: transform .2s linear;
        &:hover{
            transform: translateY(20px);
        }
    }
}
.main-screen__left-descr{
    margin-top: 39px;
    max-width: 150px;
    color: #9789aa;
    font-size: 24px;
    text-transform: lowercase;
    line-height: 1.05;
    font-weight: 700;
    cursor: pointer;
    
   
}
.main-screen__right{
    margin-left: 50px;
    margin-top: 40px;
}
.main-screen__right-title{
    color: #303030;
    font-size: 28px;
    line-height: 1.55;
    font-weight: 400;
    margin-bottom: 20px;
}
.main-screen__right-tags{
    display: flex;
    
}
.main-screen__tags-block{
    margin-left: 50px;
    &:first-child{
        margin-left: 0;
    }
}
.main-screen__tags-item{
    color: #f8d225;
    font-size: 28px;
    line-height: 1.55;
    font-weight: 800;
    border-width: 1px;
    border-radius: 30px;
    cursor: pointer;
    transition: color .2s linear;
    &:hover{
        color: #303030;
    }
    
}
.main-screen__about{
    display: flex;
    
}
.main-screen__about-item{
    margin-top: 15px;
    &:first-child {
        margin-right: 25px;
    }
}
.aaa{
    width: 100%;
    background: #f8d225;
    height: 140px;
    border-top-right-radius: 60px;
    border-bottom-right-radius: 60px;
    margin-bottom: -40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
    text-transform: lowercase;
    font-weight: 700;
    color: #fff;
    padding-bottom: 18px;
    
}
.main-screen__abot-item-img{
    transition: transform .3s linear;
    cursor: pointer;
    &:hover{
        transform: translateX(50px);
    }
}
.main-screen__block-logo--small{
    font-size: 25px;
}
.sticky-logo__wrapper{
    border-bottom: 1px solid #303030;
    padding-bottom: 10px;
    top: 0;
    margin-bottom: 20px;
}
.marquee {
    width: 300px;
    overflow: hidden;
    border: 1px solid #ccc;
    background: #ccc;
}
.clients__block{
    margin-top: 70px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.clients__block-item{
    
    background: #fff;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform .3s linear;
    padding: 2px 10px;
    cursor: pointer;
    margin-bottom: 50px;
    &:hover{
        transform: translateY(-30px);
    }
   
}
.page__title{
    color:#303030;
    font-size: 110px;
    line-height: 1.55;
    font-weight: 800;
    span{
        color: #9789aa;
    }
    
}
.contanct__buttons{
    display: flex;
    margin-bottom: 50px;
}
.contacts__buttons-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #d1d1d1;
    font-size: 24px;
    line-height: 1.2;
    font-weight: 800;
    margin-right: 30px;
    &:last-child{
        margin-right: 0;
    }
    .btn{
      
        width: 260px;
        text-align: center;
        margin-left: 0;
        margin-bottom: 20px;
    }
}
.contacts__subtitle{
    color: #f8d225;
    font-size: 33px;
    line-height: 1.2;
    font-weight: 800;
    max-width: 554px;

    span{
        color: #303030;
        font-size: 27px;
        font-weight: 400;
    }
}
.contacts__arrows{
    display: flex;
    margin-left: 80px;
    margin-bottom: -80px;
    margin-top: -100px;
    z-index: -1;
}
.contacts__arrows-item{
    z-index: -1;
    max-width: 250px;
    &:last-child{
        transform: translate(-140px, 25px);
    }
}
.contacts__wrapper{
    display: flex;
    padding-bottom: 100px;
    justify-content: space-between;
}
.contacts__block-img{
    position: relative;
    display: flex;
    align-items: flex-end;
}
.contacts__block-item{
    &:last-child{
        margin-bottom: 50px;
        margin-left: 25px;
    }
}
.contancts__bg{
    position: absolute;
    background: #f8d225;
    width: 100%;
    height: 160px;
    z-index: -1;
    bottom: 15px;
}

.footer{
    background: rgba(#909090, .1);
    
}
.footer__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
}
.li__maxWidth{
    max-width: 200px;
}

   
.footer__item-link{
     color: #303030;
     font-size: 20px;
     opacity: .8;
     line-height: 1.1;
     font-weight: 400;
 }
.span-yellow{
    color: #f8d225 !important;
}
.about-us__item-title{
    color: #d1d1d1;
    font-size: 49px;
    line-height: 1.55;
    font-weight: 800;
   
  
}
.about-us__item-descr{
    color: #000000;
    font-size: 20px;
    max-width: 483px;
    line-height: 1.3;
    font-weight: 400;
    opacity: .7;
    margin: 15px 0 5px 0;
    
}
.about-us__block-item{
    display: flex;
   
}
.about-us__item-img{
    margin-bottom: 100px;
}
.about-us__item-right{
    width: 55%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    align-content: flex-start;
}
.about-us__item-tabs{
    color: #f8d225;
    margin-bottom: 20px;
    margin-left: 15px;
    
    font-size: 19px;
    line-height: 1.55;
    font-weight: 700;
    border-width: 2px;
    border-radius: 30px;
    padding: 5px 15px;
    max-height: 40px;
    padding-bottom: 20px;
    border:2px solid #f8d225;
    transition: all .3s linear;
    cursor: pointer;
    &:hover{
        color: #fff;
        background: #f8d225;
    }
    &--black{
        color: #000;
        border:2px solid #000;
        &:hover{
            color: #fff;
            background: #303030;
        }
    }
}
.services__title{
    margin-top: 90px;
    border-top: 3px solid #9789aa;
    border-bottom: 3px solid #9789aa;
    color: #303030;
    font-size: 66px;
    line-height: 1.55;
    font-weight: 800;
    
}
.services__item{
    

    display: flex;
    justify-content: flex-end;
}
.right{
    width: 50%;
    color: #303030;
    font-size: 20px;
    padding: 20px 0;
    line-height: 1.55;
    font-weight: 400;
 
}